.field {
  font-size: 1rem;
  width: 100%;
}

.selectField {
  height: 2rem;
}

.textareaField {
  box-sizing : border-box;
  height: 5rem;
  resize: none;
}

.actionArea {
  margin-top: 1rem;
  line-height: 2rem;
  .submitBtn,
  .resetBtn {
    margin-right: .5rem;
    width: 100px;
    height: 30px;
    border: 1px black solid;
  }
}